import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DxMenuComponent } from 'devextreme-angular';
import { VatService } from '@modules/vat/shared';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { AuthService, Helper, Library, VPreviewInvService, sEnum, VStringService, SocketService } from '@core/index';
import { ViewAttachFileComponent } from '@shared/modules/vat-shared/components/view-attach-file/view-attach-file.component';
import { SendListEmailComponent, SendDataForTaxComponent } from '@shared/index';
import * as moment from 'moment';
import { TranslateService } from '@ngx-translate/core';
import { InvoicesService } from '@core/library/invoice/invoices.service';
import { String } from 'typescript-string-operations';
import { Subscription } from 'rxjs/Subscription';
import { AddNewDataComponent } from '@modules/send-data-tax/components/add-new-data/add-new-data.component';
import { SendDataTaxService } from '@modules/send-data-tax/shared/send-data-tax.service';
import { ShowDataInvoicesComponent } from "@modules/send-data-tax/components/show-data-invoices/show-data-invoices.component";
import { HandleInvoiceService } from '@modules/handle-invoice/shared';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  _translate: any;
  _language: string;
  currentUser: any;
  configs: any;
  bsModalSignRef: BsModalRef;
  exportSchedules: any;
  dataSource: any;
  vats: any;
  objParams: any = {
    exportSchedulesDefault: '',
    startDate: '',
    endDate: ''
  };
  constructor(
    public translate: TranslateService,
    private authService: AuthService,
    private modalService: BsModalService,
    private router: Router,
    private route: ActivatedRoute,
    private taxService: SendDataTaxService,
    private handleInvoiceService: HandleInvoiceService,
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    this.currentUser = this.authService.getCurrentUser();
    translate.use(this._language);
  }
  ngOnInit() {
    this.configs = this.authService.getConfigs();
    this.exportSchedules = Helper.getExportSchedule(this._translate, true).schedule;
    this.objParams.exportSchedulesDefault = "THANG_NAY";
    let oKyXuat = this.exportSchedules.filter(p => p.code == "THANG_NAY")[0];
    if (oKyXuat) {
      this.objParams.startDate = oKyXuat.startDate;
      this.objParams.endDate = oKyXuat.endDate;
    }
    this.loadList();
    this.loadVat();
  }
  loadList(params?) {
    let param = "";
    let _indexfilter_groups = 888;
    if (this.objParams.startDate) {
      this.objParams.startDate = moment(this.objParams.startDate).format('YYYY-MM-DD 00:00:00');
      param += VStringService._removeNewline(`filter_groups[${_indexfilter_groups}][filters][0][key]=created_at
        &filter_groups[${_indexfilter_groups}][filters][0][value]=${this.objParams.startDate}
        &filter_groups[${_indexfilter_groups}][filters][0][operator]=gte&`);
      _indexfilter_groups++;
    }
    if (this.objParams.endDate) {
      this.objParams.endDate = moment(this.objParams.endDate).format('YYYY-MM-DD 23:59:59');
      param += VStringService._removeNewline(`filter_groups[${_indexfilter_groups}][filters][2][key]=created_at
        &filter_groups[${_indexfilter_groups}][filters][2][value]=${this.objParams.endDate}
        &filter_groups[${_indexfilter_groups}][filters][2][operator]=lt&`);
      _indexfilter_groups++;
    }
    this.dataSource = {};
    const ref = {};
    if (param != "") {
      const _lst = this.taxService.getAll(param);
      this.dataSource.store = _lst;
    } else {
      this.dataSource.store = this.taxService.getAll();
    }
  }
  themmoi() {
    let data = {
      is_last: true,
      period_type: "",
      period: ""
    }
    Library.showloading();
    let sub = this.taxService.getInfoCreate(data).subscribe(
      rs => {
        if (rs.status == "success") {
          let initialState: any;
          if (rs.data && rs.data.id) {
            initialState = {
              period_type: rs.data.period_type,
              is_create_first: false,
              vats: this.vats
            };
          }
          else {
            initialState = {
              period_type: "T",
              is_create_first: true,
              vats: this.vats
            };
          }
          this.bsModalSignRef = this.modalService.show(AddNewDataComponent, {
            backdrop: 'static',
            keyboard: true,
            class: 'sendTax modal-lg modal-margin-width',
            initialState
          });
          const subModal = this.modalService.onHide.subscribe(() => {
            subModal.unsubscribe();
          });
        }
        else {
          Library.notify(rs.message, "error", 5000);
        }
      },
      err => {
      },
      () => {
        Library.hideloading();
        sub.unsubscribe();
      }
    );
  }
  thongdieptruyennhan(data) {
    Library.showloading();
    let message = {
      message_code: data.message_code
    }
    let sub = this.handleInvoiceService.getInforSendRecieve(message).subscribe(
      (rs: any) => {
        if (rs.status == "success") {
          let lstErr = rs.data;
          const initialState = {
            lstErr: lstErr,
            notifyType: "THONG_DIEP_TRUYEN_NHAN",
            send_wrong_status: data.status
          };
          this.bsModalSignRef = this.modalService.show(SendDataForTaxComponent, {
            backdrop: 'static',
            keyboard: true,
            class: 'modal-lg modal-margin-width truyen-nhan',
            initialState
          });
          const subModal = this.modalService.onHide.subscribe(() => {
            subModal.unsubscribe();
          });
        }
        else {
          Library.notify(rs.message, "error");
        }
      },
      err => {
      },
      () => {
        Library.hideloading();
        sub.unsubscribe();
      }
    );
  }
  onExportSchedules(e) {
    const selectedItem = e.component.option('selectedItem');
    this.objParams.exportSchedulesDefault = selectedItem.code;
    this.objParams.startDate = selectedItem.startDate;
    this.objParams.endDate = selectedItem.endDate;
    this.loadList();
  }
  onStartDate(e) {
    if (e.event) {
      this.objParams.startDate = (e.value) ? moment(e.value).format('YYYY-MM-DD HH:mm:ss') : '';
      this.loadList();
    }
  }

  onEndDate(e) {
    if (e.event) {
      this.objParams.endDate = (e.value) ? moment(e.value).format('YYYY-MM-DD HH:mm:ss') : '';
      this.loadList();
    }
  }
  formatDate(date, period_type) {
    let result = "";
    if (date) {
      switch (period_type) {
        case 'N':
          result = moment(Helper.processDate(date)).format('DD/MM/YYYY')
          break;
        case 'T':
          let dateTimeT = moment(Helper.processDate(date)).format('MM/YYYY');
          if (dateTimeT == "Invalid date")
            dateTimeT = date;
          result = "Thng " + dateTimeT;
          break;
        case 'Q':
          let dateTime = moment(Helper.processDate(date)).format('MM/YYYY');
          if (dateTime == "Invalid date")
            dateTime = date;
          result = "Qu " + dateTime;
          break;
      }
    }
    return result;
  }
  edit(data) {
    Library.showloading();
    let sub = this.taxService.laythongtinbangtonghop(data.id).subscribe(
      rs => {
        if (rs.status == "success") {
          let kydulieu = "";
          if (rs.data.period) {
            kydulieu = moment(rs.data.period).format("DD/MM/YYYY");
            if (rs.data.period_type == "Q" || rs.data.period_type == "T")
              kydulieu = moment(rs.data.period).format("MM/YYYY");
            if (kydulieu == "Invalid date")
              kydulieu = rs.data.period;
          }
          let frmData =
          {
            period_type: rs.data.period_type,
            period: rs.data.period,
            number: rs.data.send_number,
            product_type: rs.data.product_type
          }
          const initialState = {
            isUpdate: true,
            isAutoLoadData: false,
            vats: this.vats,
            frmData: frmData,
            is_first: rs.data.is_first,
            external_invoices: rs.data.external_invoices,
            kydulieu: kydulieu,
            langui: rs.data.send_number,
            id: rs.data.id
          };
          this.taxService.kydulieu = kydulieu;
          this.taxService.langui = rs.data.send_number;
          this.taxService.isAutoLoadData = false;
          this.taxService.vats = this.vats;
          this.taxService.frmData = frmData;
          this.taxService.isUpdate = true;
          this.taxService.is_first = rs.data.is_first;
          this.taxService.external_invoices = rs.data.external_invoices;
          this.taxService.id = rs.data.id;
          this.router.navigate(['system/send-data-tax/create']);
        }
        else {
          Library.notify(rs.message, "error", 5000);
        }
      },
      err => {

      },
      () => {
        Library.hideloading();
        sub.unsubscribe();
      }
    );
  }
  view(data) {
    let id = data.id;
    let extend = {
      is_external: data.is_external
    }
    Library.showloading();
    this.taxService.getFilePdf(id, extend).subscribe(
      (rs: any) => {
        if (rs.status == "success") {
          if (VStringService._getValueToString(rs.data.fileData) != "")
            Helper.showFileXml(rs.data.fileName, 'pdf', rs.data.fileData);
          else
            Library.notify("Ni dung file khng c d liu", "error", 5000);
        }
        else
          Library.notify(rs.message, "error", 5000);
      },
      err => {

      },
      () => {
        Library.hideloading();
      }
    );
  }
  delete(data) {
    const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
    result.then((dialogResult) => {
      if (dialogResult) {
        let id = data.id;
        let is_external = false;
        if (data.is_external)
          is_external = true;
        let objSend = {
          is_external: is_external
        };
        Library.showloading();
        let sub = this.taxService.xoaBangTongHop(id, objSend).subscribe(
          rs => {
            if (rs.status == "success") {
              this.loadList();
            }
            else {
              Library.notify(rs.message, "error", 5000);
            }
          },
          err => {
          },
          () => {
            Library.hideloading();
            sub.unsubscribe();
          }
        );
      }
    });
  }
  loadVat() {
    this.taxService.loadVat().subscribe(
      rs => {
        if (rs.status == "success") {
          this.vats = rs.data.DM_TY_LE_VAT;
        }
      },
      err => {

      },
      () => {

      }
    );
  }
}
