import { Component, OnInit, ViewChild, ViewChildren } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { DxFormComponent } from 'devextreme-angular';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { CoreService, AuthService, Library, sEnum, SocketService, Helper, VStringService } from '@core/index';
import { ShowDataInvoicesComponent } from "@modules/send-data-tax/components/show-data-invoices/show-data-invoices.component";
import * as moment from 'moment';
import { SendDataTaxService } from '@modules/send-data-tax/shared/send-data-tax.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Parser } from '@angular/compiler';
@Component({
  selector: 'app-add-new-data',
  templateUrl: './add-new-data.component.html',
  styleUrls: ['./add-new-data.component.scss']
})
export class AddNewDataComponent implements OnInit {
  @ViewChild(DxFormComponent) form: DxFormComponent;
  formatTime: any = "dd/MM/yyyy";
  frmData =
    {
      period_type: '',
      period: '',
      number: null,
      product_type: null
    }
  loaikydulieu = [
    { name: "Ngy", code: 'N' },
    { name: "Thng", code: 'T' },
    { name: "Qu", code: 'Q' },
  ]
  langui = [
    { name: "Ln u", code: '0' },
    { name: "B sung ln th", code: '1' },
  ]
  loaihanghoa = [
    { name: "Xng du", code: 1 },
    { name: "Vn ti hng khng", code: 2 },
    { name: "Khc", code: 9 },
  ];
  _translate: any;
  isLanDau: any;
  bsModalSignRef: BsModalRef;
  vats: any;
  period_type: any;
  is_create_first: any;
  radLanGui: any;
  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    private modalService: BsModalService,
    private taxService: SendDataTaxService,
    private router: Router,
    public translate: TranslateService,
    private route: ActivatedRoute) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }

  ngOnInit() {
    this.frmData.period_type = this.period_type;
    switch (this.period_type) {
      case "T":
        this.formatTime = "MM/yyyy";
        break;
      case "Q":
        this.formatTime = "MM/yyyy";
        break;
      case "N":
        this.formatTime = "dd/MM/yyyy";
        break;
    }
    this.frmData.period = this._getKyDuLieu(this.frmData.period_type);
    if (this.is_create_first) {
      this.frmData.product_type = 9;
      this.frmData.number = 1;
      this.radLanGui = '0';
    }
    else {
      this.radLanGui = '1';
      let kydulieu = moment(this.frmData.period).format("DD/MM/YYYY");
      if (this.frmData.period_type == "Q" || this.frmData.period_type == "T")
        kydulieu = moment(this.frmData.period).format("MM/YYYY");
      let data = {
        is_last: false,
        period_type: this.frmData.period_type,
        period: kydulieu
      }
      this.getInforSum(data);
    }
    // this.loadVat();
  }
  inputChange($event: any) {
    // if ($event.srcElement.name === 'count_date') {
    //   this.configs.count_date = $event.srcElement.valueAsNumber;
    // }
  }
  getInforSum(data) {
    Library.showloading();
    let sub = this.taxService.getInfoCreate(data).subscribe(
      rs => {
        if (rs.status == "success") {
          if (rs.data && rs.data.id) {
            this.frmData.number = rs.data.send_number ? parseInt(rs.data.send_number) + 1 : null;
            this.frmData.product_type = parseInt(rs.data.product_type);
            this.radLanGui = '1';
          }
          else {
            this.radLanGui = '0';
            this.frmData.number = 1;
            this.frmData.product_type = 9;
          }
        }
        else {
          Library.notify(rs.message, "error", 5000);
        }
      },
      err => {
      },
      () => {
        Library.hideloading();
        sub.unsubscribe();
      }
    );
  }
  changeDateTime(e) {
    if (e.value == "T" || e.value == "Q") {
      this.formatTime = "MM/yyyy";
    }
    else
      this.formatTime = "dd/MM/yyyy";
    this.frmData.period = this._getKyDuLieu(e.value);
  }
  dong() {
    this.bsModalRef.hide();
  }
  changedlangui(e) {
    if (e.value == "0") {
      this.isLanDau = true;
      this.frmData.number = null;
    }
    else {
      this.isLanDau = false;
    }
  }
  _getKyDuLieu(reportPeriod) {
    let kydulieu: any;
    let date = moment();
    let year = date.year();
    let month = date.month() < 12 ? date.month() + 1 : 1
    if (reportPeriod == "Q") {
      if (month >= 1 && month <= 3)
        kydulieu = moment(Helper.processDate("01/" + "01" + "/" + year)).format('YYYY-MM-DD HH:mm:ss');//"01" + "/" + year;
      else if (month >= 4 && month <= 6)
        kydulieu = moment(Helper.processDate("01/" + "02" + "/" + year)).format('YYYY-MM-DD HH:mm:ss');//"02" + "/" + year;
      else if (month >= 7 && month <= 9)
        kydulieu = moment(Helper.processDate("01/" + "03" + "/" + year)).format('YYYY-MM-DD HH:mm:ss');//"03" + "/" + year;
      else if (month >= 10 && month <= 12)
        kydulieu = moment(Helper.processDate("01/" + "04" + "/" + year)).format('YYYY-MM-DD HH:mm:ss');//"04" + "/" + year;
    }
    else if (reportPeriod == "T") {
      let monthYear = month < 10 ? "0" + month + "/" + year : month + "/" + year;

      kydulieu = moment(Helper.processDate("01/" + monthYear)).format('YYYY-MM-DD HH:mm:ss');
    }
    else if (reportPeriod == "N") {
      kydulieu = moment().format('YYYY-MM-DD HH:mm:ss');
    }
    return kydulieu;
  }
  TuNhapDuLieu() {
    this.openFormData(false);
  }
  TuLayDuLieu() {
    this.openFormData(true);
  }
  _validForm() {
    let result = true;
    if (VStringService._getValueToString(this.frmData.period) == "") {
      Library.notify("K d liu khng c  trng", "error");
      this.form.instance.getEditor("period").focus();
      return false;
    }
    else if (VStringService._getValueToString(this.frmData.period) != "") {
      if (this.frmData.period_type == "Q") {
        let quy = moment(this.frmData.period);
        let month = quy.month() < 12 ? quy.month() + 1 : 1
        if (month > 4) {
          Library.notify("K d liu nm trong khong Qu 01 n Qu 04", "error");
          this.form.instance.getEditor("period").focus();
          return false;
        }
      }
    }
    return result;
  }
  createObjectData() {
    let object = new Object;
    let kydulieu = moment(this.frmData.period).format("DD/MM/YYYY");
    if (this.frmData.period_type == "Q" || this.frmData.period_type == "T")
      kydulieu = moment(this.frmData.period).format("MM/YYYY");
    let langui = 0;
    if (this.isLanDau)
      langui = 1;
    else
      langui = this.frmData.number;
    object["kydulieu"] = kydulieu;
    object["langui"] = langui;
    return object;
  }
  openFormData(isAutoLoadData) {
    if (!this._validForm())
      return;
    let object = this.createObjectData();
    ///
    this.taxService.kydulieu = object["kydulieu"];
    this.taxService.langui = object["langui"];
    this.taxService.isAutoLoadData = isAutoLoadData;
    this.taxService.vats = this.vats;
    this.taxService.frmData = this.frmData;
    this.taxService.isUpdate = false;
    this.bsModalRef.hide();
    this.router.navigate(['system/send-data-tax/create'], { relativeTo: this.route });
    ///
    // const initialState = {
    //   kydulieu: object["kydulieu"],
    //   langui: object["langui"],
    //   isAutoLoadData: isAutoLoadData,
    //   vats: this.vats,
    //   frmData: this.frmData,
    //   isUpdate: false
    // };
    // this.bsModalSignRef = this.modalService.show
    //   (
    //     ShowDataInvoicesComponent,
    //     {
    //       backdrop: 'static',
    //       keyboard: false,
    //       class: 'modal-dialog modal-xl view-invoice',
    //       initialState
    //     }
    //   );
    // const subModal = this.modalService.onHide.subscribe(() => {
    //   subModal.unsubscribe();
    // });
  }
  loadVat() {
    this.taxService.loadVat().subscribe(
      rs => {
        if (rs.status == "success") {
          this.vats = rs.data.DM_TY_LE_VAT;
        }
      },
      err => {

      },
      () => {

      }
    );
  }
  periodChange = (e) => {
    let kydulieu = moment(e.value).format("DD/MM/YYYY");
    if (this.frmData.period_type == "Q" || this.frmData.period_type == "T")
      kydulieu = moment(this.frmData.period).format("MM/YYYY");
    let data = {
      is_last: false,
      period_type: this.frmData.period_type,
      period: kydulieu
    }
    this.getInforSum(data);
  };
}
