import { Component, OnInit, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { AuthService, Helper, Library, VPreviewInvService, sEnum, VStringService, SocketService, VChanggeMoneyService } from '@core/index';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import ArrayStore from 'devextreme/data/array_store';
import DataSource from 'devextreme/data/data_source';
import { InvoiceSend } from "@modules/send-data-tax/shared/invoice-send";
import { DxDataGridComponent, DxFormComponent } from 'devextreme-angular';
import { SendDataTaxService } from '@modules/send-data-tax/shared/send-data-tax.service';
import { RegisterService } from '@modules/auth/shared';
import * as moment from 'moment';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';

@Component({
  selector: 'app-show-data-invoices',
  templateUrl: './show-data-invoices.component.html',
  styleUrls: ['./show-data-invoices.component.scss']
})
export class ShowDataInvoicesComponent implements OnInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  _translate: any;
  _language: string;
  currentUser: any;
  configs: any;
  kydulieu: any;
  langui: any;
  isAutoLoadData: any;
  synthesize_datas: ArrayStore;
  dataSource: DataSource;
  invoiceSend: InvoiceSend = new InvoiceSend();
  vats: any;
  frmData: any;
  sobangtonghop: number;
  valueProgress: number = 0;
  errorImport: boolean = false;
  subFile: any;
  showPopup = false;
  totalFile: number;
  fileIndex = 0;
  tempKiFile = 0;
  lstFile: any;
  infoDangKy: any;
  isTT78: boolean = false;
  ioSignature: any;
  formatter: any = {};
  isUpdate: any;
  is_first: boolean = false;
  external_invoices: any;
  isExportFailed: boolean = false;
  id: any;
  isShowSoBangTongHop: boolean = false;
  bsModalRef: BsModalRef;
  maxRowXangDau: number = 8000;
  maxRowKhac: number = 5000;
  oCaption: any;
  format = (value) => {
    let percent = Math.round(value * 100);
    return this._translate.NOTIFY.finish + ": " + percent + '%';
  }
  processStatus = [
    {
      name: "Mi",
      code: "0"
    },
    {
      name: "Hy",
      code: "1"
    },
    {
      name: "iu chnh",
      code: "2"
    },
    {
      name: "Thay th",
      code: "3"
    },
    {
      name: "Gii trnh",
      code: "4"
    },
    {
      name: "Sai st do tng hp",
      code: "5"
    }
  ];
  constructor(
    public translate: TranslateService,
    private authService: AuthService,
    private taxService: SendDataTaxService,
    private socketService: SocketService,
    private registerService: RegisterService,
    private router: Router,
    private location: Location,
    private activeRoute: ActivatedRoute,
    private modalService: BsModalService,) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);
  }

  ngOnInit() {
    // Gn d liu t service
    this.kydulieu = this.taxService.kydulieu;
    this.langui = this.taxService.langui;
    this.isAutoLoadData = this.taxService.isAutoLoadData;
    this.vats = this.taxService.vats;
    this.frmData = this.taxService.frmData;
    this.isUpdate = this.taxService.isUpdate;
    this.is_first = this.taxService.is_first;
    this.external_invoices = this.taxService.external_invoices;
    this.id = this.taxService.id;
    this.connectWebsocket();
    this.configs = this.authService.getConfigs();
    this.formatter = VChanggeMoneyService.getFormatterColumnInv(this.configs.decimal_places_json);
    this.currentUser = this.authService.getCurrentUser();
    if (this.currentUser.is_convert_TT78) {
      this.infoDangKy = this.authService.getInfoRegister();
      if (this.infoDangKy && this.infoDangKy.id) {
        if (this.infoDangKy.status == "CHAP_NHAN")
          this.isTT78 = true;
      }
    }
    if (this.isAutoLoadData) {
      this.loadListInvoice();
    }
    else {
      if (this.isUpdate) {
        this.synthesize_datas = new ArrayStore({
          key: 'id',
          data: this.external_invoices
        });
        this.dataSource = new DataSource({
          store: this.synthesize_datas
        });
      }
      else {
        this.synthesize_datas = new ArrayStore({
          key: 'id',
          data: this.invoiceSend.add_invoices()
        });
        this.dataSource = new DataSource({
          store: this.synthesize_datas
        });
      }
    }
    this.oCaption = 
    {
      mst_khach_hang : this._translate.CONTROLL.LABEL.mst + " " + this._translate.GRID.INVOICE.customer.toLowerCase(),
      tong_thanh_tien: this._translate.GRID.INVOICE_DETAILS.sum + " " + this._translate.GRID.INVOICE_DETAILS.amount.toLowerCase(),
      loai_hd_lien_quan : this._translate.CONTROLL.LABEL.type + " " + this._translate.CONTROLL.LABEL.invoice_involve,
      mau_so_hd_lien_quan : this._translate.GRID.INVOICE.template_code + " " + this._translate.CONTROLL.LABEL.invoice_involve,
      ky_hieu_hd_lien_quan : this._translate.GRID.INVOICE.invoice_series + " " + this._translate.CONTROLL.LABEL.invoice_involve,
      so_hd_lien_quan : this._translate.CONTROLL.LABEL.number + " " + this._translate.CONTROLL.LABEL.invoice_involve,
      loai_ky_du_lieu_dieu_chinh : this._translate.CONTROLL.LABEL.data_period_type + " " + this._translate.CONTROLL.BUTTON.adjusted.toLowerCase(),
      ky_du_lieu_dieu_chinh : this._translate.CONTROLL.LABEL.data_period + " " + this._translate.CONTROLL.BUTTON.adjusted.toLowerCase(),
      gui_du_lieu: this._translate.CONTROLL.BUTTON.send + " " + this._translate.CONTROLL.LABEL.data.toLowerCase(),
      bang_tong_hop_du_lieu: this._translate.MENU.send_data_tax.toUpperCase() + " " + this._translate.CONTROLL.LABEL.invoice.toUpperCase(),
      so_tong_hop_du_lieu: this._translate.CONTROLL.LABEL.number + " " + this._translate.CONTROLL.LABEL.general_table.toLowerCase(),
    }
  }
  
  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDon().subscribe(message => {
      const data: any = message;
      if (data.Status == 'success') {
        if (data.SvcName === 'efy_Sign') {
          let i = this.fileIndex;
          if (data.Data.Name === 'hoadon.xml') {
            this.goiApiGuiFile(data.Data.Content);
          }
          else if (data.Data.Name === 'ten_filek_the_trung-dc-nhi.xml') {
            this.xuLyApiKhiXuat(this.lstFile[i]);
          }
        }
      }
      else {
        this.isExportFailed = true;
        this.continuteExportInvoice(false);
      }

    });
  }
  GuiDuLieu() {
    const question = Library.confirm("Bn c chc chn gi d liu cho CQT khng?", this._translate.NOTIFY.notify);
    question.then(
      (result) => {
        if (result) {
          if (!this.isUpdate) {
            if (this.langui == 1)
              this.is_first = true;
          }
          if (this.isAutoLoadData) {
            let data = {
              period_type: this.frmData.period_type,
              period: this.kydulieu,
              number: this.sobangtonghop,
              is_first: this.is_first,
              product_type: this.frmData.product_type,
              addition: this.frmData.number,
              add_hands: false
            };
            Library.showloading();
            let sub = this.taxService.invoicesSummary(data).subscribe(
              rs => {
                if (rs.status == "success") {
                  if (rs.data && rs.data.length > 0) {
                    this.showPopup = true;
                    this.lstFile = rs.data;
                    //this.id = rs.data[0].summary_id;
                    this.totalFile = rs.data.length;
                    this.fileIndex = 0;
                    this.tempKiFile = 0;
                    this.LayFileDuLieu();
                  }
                  else
                    Library.notify("Khng c d liu gi bng tng hp", "error", 5000);
                }
                else {
                  Library.notify(rs.message, "error", 5000);
                }
              },
              err => {

              },
              () => {
                Library.hideloading();
                sub.unsubscribe();
              }
            );
          }
          else {
            let invoices = [];
            this.synthesize_datas.load().then((dataSource) => {
              invoices = dataSource.filter(p => VStringService._getValueToString(p.invoice_number) != "" || VStringService._getValueToString(p.product_name) != "");
              invoices.forEach(item => {
                if (item.invoice_date)
                  item.invoice_date = moment(item.invoice_date).format('YYYY-MM-DD HH:mm:ss');
              });
            });
            let data = {
              period_type: this.frmData.period_type,
              period: this.kydulieu,
              number: this.sobangtonghop,
              is_first: this.is_first,
              product_type: this.frmData.product_type,
              addition: this.frmData.number,
              add_hands: true,
              invoices: invoices
            };
            Library.showloading();
            if (this.isUpdate) {
              let sub = this.taxService.updateSummaryHand(this.id, data).subscribe(
                rs => {
                  if (rs.status == "success") {
                    if (rs.data && rs.data.length > 0) {
                      this.showPopup = true;
                      this.lstFile = rs.data;
                      this.totalFile = rs.data.length;
                      this.fileIndex = 0;
                      this.tempKiFile = 0;
                      this.LayFileDuLieu();
                    }
                    else
                      Library.notify("Khng c d liu gi bng tng hp", "error", 5000);
                  }
                  else {
                    Library.notify(rs.message, "error", 5000);
                  }
                },
                err => {

                },
                () => {
                  Library.hideloading();
                  sub.unsubscribe();
                }
              );
            }
            else {
              let sub = this.taxService.invoicesSummaryHand(data).subscribe(
                rs => {
                  if (rs.status == "success") {
                    if (rs.data && rs.data.length > 0) {
                      this.showPopup = true;
                      this.lstFile = rs.data;
                      this.totalFile = rs.data.length;
                     // this.id = rs.data[0].summary_id;
                      this.fileIndex = 0;
                      this.tempKiFile = 0;
                      this.LayFileDuLieu();
                    }
                    else
                      Library.notify("Khng c d liu gi bng tng hp", "error", 5000);
                  }
                  else {
                    Library.notify(rs.message, "error", 5000);
                  }
                },
                err => {

                },
                () => {
                  Library.hideloading();
                  sub.unsubscribe();
                }
              );
            }

          }
        }
        else {
        }
      }
    );
  }
  LayFileDuLieu() {
    let i = this.fileIndex;
    if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
      || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
      this.xuLyApiKhiXuat(this.lstFile[i]);
    } else {
      if (this.isTT78) {
        let ListSerialNumber = "DSCKS";
        if (this.infoDangKy && this.infoDangKy.registration_signature) {
          let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
          if (signatures && signatures.length > 0)
            ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
        }
        let invoiceDate = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
        this.socketService.kiSo(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice', invoiceDate, "", ListSerialNumber, true);
      }
      else
        this.socketService.kiSo(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice')
    }
  }
  xuLyApiKhiXuat(oFile) {
    let xmlInvoice = "";
    let mst = this.currentUser.account.tax_code;
    this.id = oFile.summary_id;
    let subFile = this.taxService.getInfoFile(oFile.file_id, mst).subscribe(
      rs => {
        if (rs.status == "success") {
          xmlInvoice = rs.data.file_content;
          let sign_date = moment(rs.data.sign_date).format('YYYY-MM-DD HH:mm:ss');
          if (xmlInvoice != "") {
            if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
              || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing))
              this.goiApiGuiFile(xmlInvoice);
            else
              this.xuLyKiFile(xmlInvoice, sign_date);
          }
        }
        else {
          this.isExportFailed = true;
          this.continuteExportInvoice(false);
        }
      },
      err => {

      },
      () => {
        subFile.unsubscribe();
      }
    );
  }
  xuLyKiFile(xmlInvoice, invoiceDate) {
    let nodeSign = "//BTHDLieu/DSCKS/NNT";
    let ListSerialNumber = "DSCKS";
    if (this.infoDangKy && this.infoDangKy.registration_signature) {
      let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
      if (signatures && signatures.length > 0)
        ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
    }
    this.socketService.kiSo(() => this.connectWebsocket(), xmlInvoice, 'hoadon.xml', nodeSign, invoiceDate, 'generelData', ListSerialNumber, true);
  }
  goiApiGuiFile(xml_string) {
    let data = {
      xml_file: xml_string,
      id: this.id
    }
    this.subFile = this.taxService.sendXmlInvoice(data).subscribe(rs => {
      let isSuccess = false;
      this.tempKiFile = 0;
      if (rs.status == 'success') {
        isSuccess = true;
      } else {
        this.isExportFailed = true;
      }
      this.continuteExportInvoice(isSuccess);
    }, err => { }, () => {
      this.subFile.unsubscribe();
    });
  }
  GhiTam() {
    if (!this.isUpdate) {
      if (this.langui == 1)
        this.is_first = true;
    }
    let invoices = [];
    this.synthesize_datas.load().then((dataSource) => {
      invoices = dataSource.filter(p => VStringService._getValueToString(p.invoice_number) != "" || VStringService._getValueToString(p.product_name) != "");
      invoices.forEach(item => {
        if (item.invoice_date)
          item.invoice_date = moment(item.invoice_date).format('YYYY-MM-DD HH:mm:ss');
      });
    });
    let data = {
      period_type: this.frmData.period_type,
      period: this.kydulieu,
      number: this.sobangtonghop,
      is_first: this.is_first,
      product_type: this.frmData.product_type,
      addition: this.frmData.number,
      add_hands: true,
      invoices: invoices
    };
    Library.showloading();
    if (this.isUpdate) {
      let sub = this.taxService.updateSummaryHand(this.id, data).subscribe(
        rs => {
          if (rs.status == "success") {
            Library.notify("Ghi tm thnh cng", "success", 5000);
            this.router.navigate(['/system/send-data-tax'], { relativeTo: this.activeRoute });
          }
          else {
            Library.notify(rs.message, "error", 5000);
          }
        },
        err => {
        },
        () => {
          Library.hideloading();
          sub.unsubscribe();
        }
      );
    }
    else {
      let sub = this.taxService.invoicesSummaryHand(data).subscribe(
        rs => {
          if (rs.status == "success") {
            Library.notify("Ghi tm thnh cng", "success", 5000);
            this.router.navigate(['/system/send-data-tax'], { relativeTo: this.activeRoute });
          }
          else {
            Library.notify(rs.message, "error", 5000);
          }
        },
        err => {
        },
        () => {
          Library.hideloading();
          sub.unsubscribe();
        }
      );
    }
  }
  dong() {
    // this.location.back();
    this.router.navigate(['/system/send-data-tax'], { relativeTo: this.activeRoute });
    // window.location.reload();
    // this.router.navigate['/system/send-data-tax']
  }
  onContextMenuPreparing(e) {
    if (e.row && e.row.rowType === 'data') {
      if (!this.isAutoLoadData) {
        e.items = [
          {
            text: this._translate.CONTROLL.BUTTON.add_row,
            onItemClick: () => {
              this.addRow();
            }
          },
          {
            text: this._translate.CONTROLL.BUTTON.delete_row,
            onItemClick: () => {
              this.deleteRow(e.row.data);
            }
          },
        ];
      }
    }
  }
  /* Thm dng */
  addRow() {
    let len, order, orderLast;
    this.synthesize_datas.load().then((dataSource) => {
      len = dataSource.length;
      if (len == 0)
        orderLast = 0;
      else
        orderLast = dataSource[len - 1].order;
    });

    for (let i = 0; i < 1; i++) {

      if (len >= 1) {
        order = ++orderLast;
      } else {
        order = 1;
      }
      const data = this.invoiceSend.addData(order);
      this.synthesize_datas.insert(data);
    }
    this.dataGrid.instance.refresh();
  }
  deleteRow(item) {
    setTimeout(() => {
      const check = confirm(this._translate.CONFIRM.delete);
      if (check) {
        setTimeout(() => {
          this.synthesize_datas.remove(item.id);
          let order = 0;
          let isOneRow = false;
          this.synthesize_datas.load().then((dataSource) => {
            dataSource.forEach((item, index) => {
              item.order = ++order;
            });
            if (dataSource.length == 0)
              isOneRow = true;
          });
          this.dataGrid.instance.refresh();
          if (isOneRow)
            this.addRow();
        }, 100);
      }
    }, 200);
  }
  onImportShowing(e) {
    this.valueProgress = 0;
  }
  onImportHiding(e) {
    let r = true;
    if (this.valueProgress < 100 && this.errorImport == false) {
      r = confirm(this._translate.CONFIRM.sign_stop);
    }
    if (r) {
      if (this.subFile) {
        this.subFile.unsubscribe();
      }
      this.valueProgress = 0;
    } else {
      e.cancel = true;
    }
  }
  continuteExportInvoice(isSuccess) {
    let percentComplete = 100 / this.totalFile;
    this.valueProgress += percentComplete;
    if (this.fileIndex == this.totalFile - 1) {
      if (!this.isExportFailed && isSuccess)
        Library.notify("Gi bng tng hp thnh cng", 'success', 5000);
      else {
        Library.notify("C 1 s ha n cha gi ln CQT", 'success', 5000);
      }
      this.showPopup = false;
      this.router.navigate(['/system/send-data-tax'], { relativeTo: this.activeRoute });
    }
    else if (this.fileIndex < this.totalFile - 1) {
      this.fileIndex++;
      this.LayFileDuLieu();
    }
  }
  loadListInvoice() {
    if (this.langui == 1)
      this.is_first = true;
    let data =
    {
      "period_type": this.frmData.period_type,
      "period": this.kydulieu,
      "is_first": this.is_first,
      "number": this.sobangtonghop,
      "product_type": this.frmData.product_type,
      "addition": this.frmData.number,
    }
    this.synthesize_datas = this.taxService.getInvoices(data, 'bang-tong-hop');
    this.dataSource = new DataSource({
      store: this.synthesize_datas
    });
    //  this.dataSource.store = 
  }
}
