import * as moment from 'moment';
import { Helper } from '@core/helper/Helper';

export class InvoiceSend {
  constructor() {
  }
  id: string = '';
  id_invoice: string = '';
  is_external: boolean = false;/* C phi d liu c thm mi trn li k? */
  order: number = null;/* S th t */
  template_code: string = '';/* Mu s */
  invoice_series: string = '';/* K hiu */
  customer_name: string = '';/* Tn khch hng */
  buyer_tax_code: string = '';/* MST khch hng */
  customer_code: string = '';/* M khch hng */
  product_code: string = '';/* M hng ha, dch v */
  product_name: string = '';/* Tn hng ha, dch v */
  quantity: number = null;/* S lng */
  amount: number = null;/* Tng thnh tin */
  vat: string = '';/* Thu sut */
  amount_vat: number = null;/* Tng tin thu */
  amount_after_vat: number = null;/* Tng tin thanh ton */
  status: string = '';/* Trng thi */
  related_invoice_type: string = '';/* Loi H lin quan */
  related_template_code: string = '';/* Mu s  H lin quan */
  period_type: string = '';/* Loi k d liu iu chnh */
  period: string = '';/* K d liu iu chnh */
  note: string = '';/* Ghi ch */
  invoice_date: string = "";//Ngy ha n
  invoice_number: string = ""; //S ha n
  buyer_name: string = "";
  related_invoice_series: string = '';/* K hiu  H lin quan */
  related_invoice_number: string = '';/* S  H lin quan */
  decimal_places_json: string = "";
  add_invoices() {
    let arr = [];
    for (let i = 0; i < 10; i++) {
      arr.push({
        id: Helper.makeid(),
        id_invoice: "",
        is_external: true,/*  */
        order: i + 1,
        template_code: "",
        invoice_series: "",
        customer_name: "",
        buyer_tax_code: "",
        customer_code: "",
        product_code: "",
        product_name: "",
        quantity: null,
        amount: null,
        vat: "",
        amount_vat: null,
        amount_after_vat: null,
        status: "",
        related_invoice_type: "",
        related_template_code: "",
        period_type: "",
        period: "",
        note: "",
        invoice_date: "",
        invoice_number: "",
        buyer_name: "",
        related_invoice_series: "",
        related_invoice_number: "",
        decimal_places_json: ""
      });
    }
    return arr;
  }
  addData(order) {
    return {
      id: Helper.makeid(),
      is_external: true,/*  */
      id_invoice: "",
      order: order,
      template_code: "",
      invoice_series: "",
      customer_name: "",
      buyer_tax_code: "",
      customer_code: "",
      product_code: "",
      product_name: "",
      quantity: null,
      amount: null,
      vat: "",
      amount_vat: null,
      amount_after_vat: null,
      status: "",
      related_invoice_type: "",
      related_template_code: "",
      period_type: "",
      period: "",
      note: "",
      invoice_date: "",
      invoice_number: "",
      buyer_name: "",
      related_invoice_series: "",
      related_invoice_number: "",
      decimal_places_json: ""
    }
  }
}
