import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexComponent } from './components/index/index.component';
import { ShowDataInvoicesComponent } from "@modules/send-data-tax/components/show-data-invoices/show-data-invoices.component";
import { DataTaxResolver } from "@modules/send-data-tax/shared/data-tax.resolver";
import { DataTaxEditResolver } from "@modules/send-data-tax/shared/edit-data-tax.resolver";
import {VatDeactivateGuard} from '@modules/vat/deactivate/vat-deactivate.guard';
const routes: Routes = [
  {
    path: '',
    component: IndexComponent
  },
  {
    path: 'create',
    component: ShowDataInvoicesComponent,
    data: { brec: 'create_summary_data' }
  },
  {
    path: ':Id',
    component: ShowDataInvoicesComponent,
    resolve: { init: DataTaxEditResolver },
    data: { brec: 'invoice_details' }
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SendDataTaxRoutingModule { }
